export default {
	data() {
		return {
			content: '',
			contentCount: 0,
			checked: false,
			radios: [{value: '1', checked: true, name: '公开'}, {value: '0', checked: false, name: '不公开'}]
		}
	},
	props:{
		value:{
			type: Boolean,
			default: false
		},
		replyInfo: {
			type: String,
			default: ''
		},
		role: {
			type: String,
			default: ''
		},
	},
	methods: {
		inputChange(e) {
			this.contentCount = e.detail.value.length;
		},
		valueFlag() {
			this.$emit('input', false);
		},
		gobackTop() {
		    uni.pageScrollTo({
		        scrollTop: 0
		    });
		},
		radioChange(evt){
			for (let i = 0; i < this.radios.length; i++) {
			    if (this.radios[i].value === evt.target.value) {
			        this.radios[i].checked = true;
			    } else {
					this.radios[i].checked = false;
				}
			}
		},
		checkboxChange(e) {
		                this.checked = !this.checked;
		            },
		onConfirm() {
			const len = this.radios.length;
			let isPublish = null;
			for (let i=0; i< len; i++) {
				if (this.radios[i].checked) isPublish = Number(this.radios[i].value);
			}
			const obj = {
				content: this.content,
				isPublish,
				isAnonymous: this.checked ? 1 : 0,
			};
			this.$emit('onConfirm', obj);
		},
	},
}
